.footer {
    background: -webkit-gradient(linear, right top, left top, from(#00dbff), color-stop(#d9d9d9), to(#f590fb));
    background: -webkit-linear-gradient(right, #00dbff, #d9d9d9, #f590fb);
    background: -o-linear-gradient(right, #00dbff, #d9d9d9, #f590fb);
    background: linear-gradient(270deg, #00dbff, #d9d9d9, #f590fb);
    padding: 1vh;
    margin-top: 2vh;
}

.footer p {
    margin-top: 1vh;
    margin-left: auto;
    margin-right: auto;
}


